



































import { Component, Prop, Vue } from 'vue-property-decorator'
import { namespace } from 'vuex-class'
import { AuthUser } from '@/store/modules/auth.module'
import InputTextField from '@/components/InputTextField.vue'
import ActionButton from '@/components/ActionButton.vue'
const Auth = namespace('Auth')

@Component({
  components: {
    InputTextField,
    ActionButton,
  },
})
export default class Login extends Vue {
  private user = { username: '', password: '' }
  private message = ''

  @Prop()
  k?: string

  @Auth.Getter
  private isLoggedIn!: boolean

  @Auth.Getter
  private isPolicyAccepted!: boolean

  @Auth.Getter
  private isExpired!: boolean

  @Auth.Action
  private login!: (data: {
    username: string
    password: string
  }) => Promise<AuthUser>

  @Auth.Action
  private refreshToken!: () => Promise<AuthUser>

  created(): void {
    if (this.isLoggedIn && this.isPolicyAccepted) {
      // ログインしているがセッション切れ
      if (this.isExpired) {
        // refreshToken を使って再認証
        this.refreshToken().then(() => {
          this.$router.push('/record')
        })
      } else {
        this.$router.push('/record')
      }
    }
    // キーがある場合、ユーザ名/パスワードが base64 エンコーディングされている
    // @TODO SMSによるIdToken認証ができたら消す
    if (this.k) {
      console.log(this.k)
      const decoded = atob(this.k)
      this.user.username = decoded.split('/')[0]
      this.user.password = decoded.split('/')[1]
      this.handleLogin()
    }
  }

  get isSubmittable(): boolean {
    return this.user.username !== '' && this.user.password !== ''
  }

  get btnTheme(): string {
    return this.isSubmittable ? 'primary' : 'disable'
  }

  handleLogin(): void {
    if (this.user.username && this.user.password) {
      this.login(this.user).then(
        (data) => {
          if (data.policy_accepted) {
            this.$router.push('/record')
          } else {
            this.$router.push('/terms')
          }
        },
        (error) => {
          this.message = `ログインできませんでした。${error}`
        },
      )
    }
  }
}
