























import Vue from 'vue'

type Rules = {
  [key: string]: {
    isValid: boolean
    message: string
  }
}
type SizeType = 'M' | 'S'
type FontSizeType = string

export default Vue.extend({
  props: {
    id: {
      type: String,
      default: '',
    },
    value: {
      type: String,
      default: '',
    },
    type: {
      type: String,
      default: 'text',
    },
    name: {
      type: String,
      default: '',
    },
    placeholder: {
      type: String,
      default: '',
    },
    step: {
      type: Number,
      default: 0,
    },
    required: {
      type: Boolean,
      default: false,
    },
    floatingPoint: {
      type: Boolean,
      default: false,
    },
    fontSize: {
      type: String,
      default: 'M',
    },
    autocomplete: {
      type: String,
      default: 'on',
    },
    unit: {
      type: String,
      default: '',
    },
  },
  watch: {
    rules() {
      this.showError = this.hasErrors
    },
    value() {
      this.$emit('validate', !this.hasErrors)
    },
  },
  data(): {
    showError: boolean
    fontSizeMap: Map<SizeType, FontSizeType>
  } {
    return {
      showError: false,
      fontSizeMap: new Map([
        ['M', '20px'],
        ['S', '16px'],
      ]),
    }
  },
  computed: {
    rules(): Rules {
      return {
        required: {
          isValid: this.ruleRequired,
          message: '必須項目です', // TODO: メッセージを確定させる
        },
        floatingPoint: {
          isValid: this.ruleFloatingPoint,
          message: '小数点まで入力してください', // TODO: メッセージを確定させる
        },
      }
    },
    ruleRequired(): boolean {
      if (!this.required) return true
      return Boolean(this.value)
    },
    ruleFloatingPoint(): boolean {
      if (!this.floatingPoint) return true
      return this.value.match(/\d+\.\d/) != null
    },
    errorMessage(): string {
      if (!this.showError) return ''
      const key = Object.keys(this.rules).find(
        (key) => !this.rules[key].isValid,
      )
      if (!key) return ''
      return this.rules[key].message
    },
    hasErrors(): boolean {
      return Object.keys(this.rules).some(
        (key: string) => !this.rules[key].isValid,
      )
    },
  },
})
